@keyframes fadeIn {
    0% {
        opacity: 0;
    }
    75% {
        opacity: 1;
    }
    100% {
        opacity: 1;
    }
}

@keyframes ElephantAnim {
    0% {
        transform: translate(0, 0) rotate(0);
    }
    50% {
        transform: translate(10px, 0) rotate(30deg);
    }
    100% {
        transform: translate(0, 0) rotate(0);
    }
}

@keyframes LampAnim {
    0% {
        transform: rotate(0);
    }
    10% {
        transform: rotate(40deg);
    }
    40% {
        transform: rotate(40deg);
    }
    69% {
        transform: rotate(-5deg);
    }
    85% {
        transform: rotate(-25deg);
    }
    100% {
        transform: rotate(0);
    }
}

@keyframes RocketAnim {
    0% {
        transform: translate(0, 0);
    }
    67% {
        transform: translate(0, 70px);
    }
    100% {
        transform: translate(0, 0);
    }
}

@keyframes ClockAnim {
    0% {
        transform: scale(0.7) rotate(0deg);
    }

    50% {
        transform: scale(1) rotate(180deg);
    }

    100% {
        transform: scale(0.7) rotate(360deg);
    }
}

@keyframes PlaneAnim {
    0% {
        transform: scale(0.5) rotate(45deg);
    }

    50% {
        transform: scale(1) rotate(0);
    }

    100% {
        transform: scale(0.5) rotate(45deg);
    }
}

@keyframes leftDown {
    0% {
        transform: translate(0, 0);
    }

    50% {
        transform: translate(-50px, 18px);
    }

    100% {
        transform: translate(0, 0);
    }
}

@keyframes rightLeft {
    0% {
        transform: translate(0, 0);
    }

    50% {
        transform: translate(-50px, 0);
    }

    100% {
        transform: translate(0, 0);
    }
}
@keyframes DownAnim {
    0% {
        transform: translate(0, 0);
    }

    50% {
        transform: translate(0, -50px);
    }

    100% {
        transform: translate(0, 0);
    }
}

@keyframes rotate45deg {
    0% {
        transform: rotate(0);
    }

    50% {
        transform: rotate(-45deg);
    }

    100% {
        transform: rotate(0);
    }
}

@keyframes rotate360 {
    0% {
        transform: rotate(0deg);
    }

    100% {
        transform: rotate(360deg);
    }
}

@keyframes plusAnim {
    0% {
        transform: scale(1);
        opacity: 1;
    }

    30% {
        opacity: 0;
        transform: scale(1.35);
    }

    60% {
        opacity: 0;
        transform: scale(1.35);
    }

    100% {
        transform: scale(1);
        opacity: 1;
    }
}

@keyframes AtomAnim {
    0% {
        transform: rotate(0deg) scale(0);
    }

    50% {
        transform: rotate(30deg) scale(1);
    }

    100% {
        transform: rotate(0deg) scale(0);
    }
}

@keyframes pen {
    0% {
        transform: translate(0, 0) rotate(0deg);
    }

    25% {
        transform: translate(10px, 0) rotate(60deg);
    }

    50% {
        transform: translate(0, -5px) rotate(-20deg);
    }

    75% {
        transform: translate(16px, -5px) rotate(-10deg);
    }

    100% {
        transform: translate(0, 0) rotate(0deg);
    }
}

@keyframes fly-plane {
    0% {
        transform: translate(-100%, 0);
        opacity: 1;
    }
    10% {
        transform: translate(250px, 20px);
    }
    30% {
        transform: translate(700px, 200px) rotate(30deg);
    }
    50% {
        transform: translate(1200px, 400px);
    }
    60% {
        transform: translate(1400px, 440px);
    }
    70% {
        transform: translate(1500px, 400px) rotate(-20deg);
    }
    90% {
        transform: translate(2000px, 200px) rotate(-20deg);
    }
    100% {
        transform: translate(2600px, 140px);
        opacity: 1;
    }
}

@keyframes step {
    0% {
        opacity: 0;
    }
    30% {
        opacity: 1;
        transform: translate(-50px, 0);
    }
    100% {
        opacity: 1;
        transform: translate(-50px, 0);
    }
}

@keyframes fly-plane-Reg {
    0% {
        transform: translate(2600px, -800px) scale(-1, 1) rotate(20deg);
        opacity: 1;
    }
    50% {
        transform: translate(2000px, -300px) scale(-1, 1) rotate(30deg);
        opacity: 1;
    }
    90% {
        transform: translate(1600px, -100px) scale(-1, 1) rotate(20deg);
        opacity: 1;
    }
    100% {
        transform: translate(1520px, -100px) scale(-1, 1) rotate(-35deg);
        opacity: 1;
    }
}

@keyframes planeOne {
    0% {
        transform: translate(0, 0) rotate(0);
    }
    90% {
        transform: translate(-20px, 0) rotate(-5deg);
    }
    100% {
        transform: translate(-20px, 0) rotate(-5deg);
    }
}
@keyframes planeTwo {
    0% {
        transform: translate(0, 0) rotate(0);
    }
    90% {
        transform: translate(-20px, 0) rotate(20deg);
    }
    100% {
        transform: translate(-20px, 0) rotate(20deg);
    }
}
